:root {
  --color-general: #d41443;
  --color-second: #ffa5ba;
  --btn-white: #fff;
  --btn-white-hover: #ecebeb;
  --color-title: #000;
  --color-text: #907e82;
  --anim-link: 250ms cubic-bezier(.1, .7, 1, .1);
  --font-general: "DM Sans";
  --font-second: "Titan One";
  --gap: 16px;
  --top: 20px;
  --card-number: 3;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, li, ul, ol {
  margin: 0;
  padding: 0;
}

input {
  border: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  border: none;
}

ul, ol {
  list-style: none;
}

body {
  color: #fff;
  margin: 0;
  font-family: DM Sans;
  font-size: 16px;
}

body.no-scroll {
  overflow: hidden;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.btn-other {
  text-align: center;
  border-radius: 22px;
  padding: 13px 37px;
  font-size: 16px;
  line-height: 1.31;
}

.btn-hero {
  text-align: center;
  border-radius: 20px;
  padding: 11px 24px;
  font-size: 14px;
  line-height: 1.29;
}

.btn-white {
  color: var(--color-general);
  background-color: var(--btn-white);
}

.btn-white:hover, .btn-white:focus {
  background-color: #ecebeb;
}

.btn-red {
  color: var(--btn-white);
  background-color: var(--color-general);
}

.section-title {
  font-family: var(--font-second);
  color: var(--color-general);
  font-size: 34px;
}

@media screen and (min-width: 768px) {
