    font-size: 26px;
  }
}

@media screen and (min-width: 1200px) {
  .section-label {
    font-size: 30px;
  }
}

.cardset {
  margin: 0;
  margin-left: calc(-1 * var(--gap) );
  margin-top: calc(-1 * var(--top) );
  flex-wrap: wrap;
  padding: 0;
  list-style: none;
  display: flex;
}

.cardset__item {
  flex-basis: calc(100% / var(--card-number)  - var(--gap) );
  margin-left: var(--gap);
  margin-top: var(--top);
}

